// src/components/dashboard/PackagesSection.tsx
"use client";

import { useSession } from "next-auth/react";
import { useEffect, useState } from "react";
import { loadStripe } from "@stripe/stripe-js";
import { Row, Col, Button, Spinner } from "react-bootstrap";
import Image from "next/image";

interface PackageDisponible {
  id: string;
  name: string;
  sessions: number;
  price: number;
  inscription: number;
  priceId: string;
  image: string;
}

const paquetesDisponibles: PackageDisponible[] = [
  {
    id: "pkg1",
    name: "1 clase/semana (4 al mes)",
    sessions: 4,
    price: 1400,
    inscription: 1000,
    priceId: "price_1RJd0OFV5ZpZiouCasDGf28F",
    image: "/images/estimlacion_agua_animado.jpeg",
  },
  {
    id: "pkg2",
    name: "2 clases/semana (8 al mes)",
    sessions: 8,
    price: 2250,
    inscription: 1000,
    priceId: "price_1RJd1jFV5ZpZiouC1xXvllVc",
    image: "/images/estimulacion_piso_animado.jpeg",
  },
  {
    id: "pkg3",
    name: "3 clases/semana (12 al mes)",
    sessions: 12,
    price: 2500,
    inscription: 1000,
    priceId: "price_1RJd2fFV5ZpZiouCsaJNkUTO",
    image: "/images/quiro_animado.jpeg",
  },
  // … añade los demás paquetes iguales al const anterior,
  // usando las rutas de imagen que corresponden en /public/images
];

export default function PackagesSection() {
  const { data: session, status } = useSession({ required: true });
  const [loadingCheckout, setLoadingCheckout] = useState<string | null>(null);

  // Espera a la sesión
  if (status === "loading") {
    return (
      <div className="text-center py-5">
        <Spinner animation="border" />
      </div>
    );
  }

  // Maneja la compra de un paquete
  const handleBuy = async (pkg: PackageDisponible) => {
    setLoadingCheckout(pkg.id);
    // Crea sesión de Checkout en nuestra API
    const res = await fetch("/api/stripe/checkout", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        lineItems: [{ price: pkg.priceId, quantity: 1 }],
        metadata: { paquete: pkg.id },
      }),
    });
    const { sessionId } = await res.json();
    const stripe = await loadStripe(process.env.NEXT_PUBLIC_STRIPE_PUBLISHABLE_KEY!);
    await stripe?.redirectToCheckout({ sessionId });
  };

  return (
    <section className="container py-4">
      <h3 className="mb-4 text-center">Mis paquetes</h3>
      <Row className="g-4">
        {paquetesDisponibles.map((pkg) => (
          <Col key={pkg.id} xs={12} md={6} lg={4}>
            <div className="card servicio-card h-100 text-center">
              <Image
                src={pkg.image}
                alt={pkg.name}
                width={300}
                height={200}
                className="card-img-top"
              />
              <div className="card-body d-flex flex-column">
                <h5 className="card-title">{pkg.name}</h5>
                <p>{pkg.sessions} sesiones/mes</p>
                <p>Inscripción: ${pkg.inscription}</p>
                <p className="h4 mt-auto">${pkg.price}</p>
                <Button
                  onClick={() => handleBuy(pkg)}
                  disabled={loadingCheckout !== null}
                  className="btn btn-orange mt-3"
                >
                  {loadingCheckout === pkg.id ? "Redirigiendo…" : "¡Lo quiero!"}
                </Button>
              </div>
            </div>
          </Col>
        ))}
      </Row>
    </section>
  );
}
